  font-display: block;
  src: url("../../node_modules/primeicons||file:./fonts/primeicons.eot");
  src: url("../../node_modules/primeicons||file:./fonts/primeicons.eot?#iefix") format("embedded-opentype"), url("../../node_modules/primeicons||file:./fonts/primeicons.woff2") format("woff2"), url("../../node_modules/primeicons||file:./fonts/primeicons.woff") format("woff"), url("../../node_modules/primeicons||file:./fonts/primeicons.ttf") format("truetype"), url("../../node_modules/primeicons||file:./fonts/primeicons.svg?#primeicons") format("svg");
  font-weight: normal;
